import React from "react";

const FormInput = ({
  iconType,
  inputMargin,
  onChange,
  isOtp,
  onClick,
  ...props
}) => {
  return (
    <div className={`input-div ${inputMargin}`}>
      <div className="i">
        <i className={`fas ${iconType}`} />
      </div>
      <div className="div">
        <input className="input" {...props} onChange={onChange} />
        {isOtp ? (
          <div className="otp-btn" onClick={onClick}>
            Resend
          </div>
        ) : (
          ""
        )}
      </div>
    </div>
  );
};

export default FormInput;
