import { apiPost, authApiPost } from "../utils/axios";

const BASE_API_URL = "https://mainsias.easify.xyz/api/auth";

export default {
  authSignUp: (payload) => apiPost(`${BASE_API_URL}/signup`, payload),
  authSignIn: (payload) => apiPost(`${BASE_API_URL}/signin`, payload),
  authOtpVerifyOrResend: (payload) =>
    apiPost(`${BASE_API_URL}/verify/email_verification`, payload),
};
