import React, { useState } from "react";
import { useHistory, Redirect } from "react-router-dom";
import { createStructuredSelector } from "reselect";
import { connect } from "react-redux";

import { Avtar, Bg } from "../icons/index";
import wave from "../img/wave.png";
import FormInput from "../components/FormInput";
import HeaderButton from "../components/HeaderButton";
import "../style.css";
import { signInStart } from "../../../redux/actions/auth";
import { selectCurrentUser } from "../../../redux/selectors/auth";

const SignIn = ({ signInStart, currentUser }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleSubmit = (event) => {
    event.preventDefault();
    signInStart(email, password);
  };
  const history = useHistory();

  if (currentUser) {
    if (!currentUser.user.isVarified) return <Redirect to={"/otp"} />;
  }

  return (
    <div>
      <img className="wave" src={wave} alt="wave" />
      <HeaderButton
        btnName="Sign Up"
        onClick={() => history.push("/sign-up")}
      />
      <div className="form-container">
        <div className="img">
          <Bg />
        </div>
        <div className="login-content">
          <form onSubmit={handleSubmit} className="sign-in-and-sign-up-form">
            <Avtar />
            <h2 className="title">Sign In</h2>
            <FormInput
              inputMargin="mt-0"
              iconType="fa-user"
              type="email"
              value={email}
              placeholder="Email"
              required
              onChange={(e) => setEmail(e.target.value)}
            />

            <FormInput
              inputMargin="mb-1"
              iconType="fa-lock"
              type="password"
              value={password}
              placeholder="Passowrd"
              required
              onChange={(e) => setPassword(e.target.value)}
            />
            <a href="#">Forgot Password?</a>
            <input
              type="submit"
              className="btn form-submit-btn"
              defaultValue="Login"
            />
          </form>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  currentUser: selectCurrentUser,
});

const mapDispatchToProps = (dispatch) => ({
  signInStart: (email, password) => dispatch(signInStart({ email, password })),
});

export default connect(mapStateToProps, mapDispatchToProps)(SignIn);
