import React, { useState } from "react";
import { useHistory, Redirect } from "react-router-dom";
import { Avtar, Bg } from "../icons/index";
import wave from "../img/wave.png";
import FormInput from "../components/FormInput";
import "../style.css";
import { otpVerifyStart, otpResendStart } from "../../../redux/actions/auth";
import { connect } from "react-redux";

const Otp = ({ currentUser, otpVerifyStart, otpResendStart }) => {
  const [otp, setOtp] = useState("");

  const handleSubmit = (event) => {
    event.preventDefault();
    otpVerifyStart(Number(otp), currentUser);
  };

  if (currentUser) {
    if (currentUser.user.isVarified) return <Redirect to={"/dashboard"} />;
  }

  const handleResendOtp = () => {
    otpResendStart(currentUser.token);
  };

  return (
    <div>
      <img className="wave" src={wave} alt="wave" />

      <div className="form-container">
        <div className="img">
          <Bg />
        </div>
        <div className="login-content">
          <form onSubmit={handleSubmit} className="sign-in-and-sign-up-form">
            <Avtar />
            <h2 className="title">OTP</h2>
            <FormInput
              inputMargin="mt-0"
              iconType="fa-user"
              type="text"
              value={otp}
              placeholder="otp"
              isOtp
              required
              onClick={handleResendOtp}
              onChange={(e) => setOtp(e.target.value)}
            />

            <button type="submit" className="btn form-submit-btn">
              Submit
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

const maptDispatchToProps = (dispatch) => ({
  otpVerifyStart: (otp, currentUser) =>
    dispatch(otpVerifyStart({ otp, currentUser })),

  otpResendStart: (token) => dispatch(otpResendStart({ token })),
});

const mapStateToProps = ({ auth: { currentUser } }) => ({
  currentUser,
});

export default connect(mapStateToProps, maptDispatchToProps)(Otp);
