import React, { useState } from "react";
import { useHistory, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { createStructuredSelector } from "reselect";

import { Avtar, Bg } from "../icons/index";
import wave from "../img/wave.png";
import FormInput from "../components/FormInput";
import HeaderButton from "../components/HeaderButton";

import { signUpStart } from "../../../redux/actions/auth";

import "../style.css";
import { selectCurrentUser } from "../../../redux/selectors/auth";

const SignUp = ({ signUpStart, currentUser }) => {
  const history = useHistory();
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleSubmit = (event) => {
    event.preventDefault();
    signUpStart(firstName, lastName, email, password);
  };
  if (currentUser) {
    if (!currentUser.user.isVarified) return <Redirect to={"/otp"} />;
  }

  return (
    <div>
      <img className="wave" src={wave} alt="wave" />
      <HeaderButton
        btnName="Sign In"
        onClick={() => history.push("/sign-in")}
      />
      <div className="form-container">
        <div className="img">
          <Bg />
        </div>
        <div className="login-content">
          <form onSubmit={handleSubmit} className="sign-in-and-sign-up-form">
            <Avtar />
            <h2 className="title">Sign Up</h2>
            <FormInput
              inputMargin="mt-0"
              iconType="fa-user"
              type="text"
              placeholder="first name"
              value={firstName}
              required
              onChange={(e) => setFirstName(e.target.value)}
            />
            <FormInput
              inputMargin="mt-0"
              iconType="fa-user"
              type="text"
              placeholder="last name"
              value={lastName}
              required
              onChange={(e) => setLastName(e.target.value)}
            />
            <FormInput
              inputMargin="mt-0"
              iconType=" fa-envelope"
              type="email"
              placeholder="Email"
              value={email}
              required
              onChange={(e) => setEmail(e.target.value)}
            />
            <FormInput
              inputMargin="mb-3"
              iconType="fa-lock"
              type="password"
              placeholder="Passowrd"
              value={password}
              required
              onChange={(e) => setPassword(e.target.value)}
            />
            <input
              type="submit"
              className="btn form-submit-btn"
              defaultValue="Login"
            />
          </form>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  currentUser: selectCurrentUser,
});

const mapDispatchToProps = (dispatch) => ({
  signUpStart: (firstName, lastName, email, password) =>
    dispatch(signUpStart({ firstName, lastName, email, password })),
});

export default connect(mapStateToProps, mapDispatchToProps)(SignUp);
